body,
html {
  height: 100%;
  display: grid;
  background: rgb(238, 174, 202);
  background: linear-gradient(30deg, rgb(238, 174, 202) 0%, rgb(148, 187, 233) 100%);
}

.click_main{
  margin: auto;
  padding: 0;
  border: none;
  border-radius: 35%;
  width: 250px;
  height: 250px;
  background: rgb(238, 174, 202);
  background: linear-gradient(30deg, rgb(238, 174, 202) 0%, rgb(148, 187, 233) 100%);
  color: rgb(195, 180, 216);
  font-size: 3.6em;
  font-weight: 600;
  box-shadow: inset 0 0 35px 0px rgba(0, 0, 0, 0.25), inset 0 2px 1px 1px rgba(255, 255, 255, 0), 1px 1px 15px 2px rgba(154, 137, 164, 0.5);
  text-shadow: 1px -1px 3px rgb(143, 162, 199), -1px 1px 3px rgb(192, 151, 178);
  outline: none;
  -webkit-transition: box-shadow 0.4s, text-shadow 0.5s;
  -moz-transition: box-shadow 0.4s, text-shadow 0.5s;
  transition: box-shadow 0.4s, text-shadow 0.5s;
}
.click_main:hover {
  cursor: pointer;
  box-shadow: inset 0 0 35px 0px rgba(0, 0, 0, 0.15), inset 0 2px 1px 1px rgba(255, 255, 255, 0), 1px 1px 15px 2px rgba(154, 137, 164, 0.1);
  text-shadow: 3px -3px 5px rgb(143, 162, 199), -3px 3px 5px rgb(192, 151, 178);
  -webkit-transition: box-shadow 0.4s, text-shadow 0.5s;
  -moz-transition: box-shadow 0.4s, text-shadow 0.5s;
  transition: box-shadow 0.4s, text-shadow 0.5s;
}
.click_main:active {
  background: linear-gradient(30deg, rgb(210, 178, 212) 0%, rgb(177, 183, 223) 100%);
  color: rgb(192, 181, 218);
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, 0.15), 1px 1px 15px 2px rgba(154, 137, 164, 0.1);
  text-shadow: 1px -1px 1px rgba(143, 162, 199, 0.75), -1px 1px 1px rgba(192, 151, 178, 0.75);
  -webkit-transition: background 0.3s color 0.2s, text-shadow 0.3s box-shadow 0.3s;
  -moz-transition: background 0.3s color 0.2s, text-shadow 0.3s box-shadow 0.3s;
  transition: background 0.3s color 0.2s, text-shadow 0.3s box-shadow 0.3s;
}

